import appAd from "../../../components/page-component/app-ad/app-ad"
import {mapState} from "vuex";

export default {
    data() {
        return {
            list: [],
            over: false,
            ad_data: {},
            user: {},
            loading: false,
            more: false,
            _num: 1,
            setting: null,
            adHeight: 0,
            coupon_url: this.$api.step.receive,
        }
    },
    components: {
        appAd
    },
    computed: {
        ...mapState({
            stepImg: state => state.mallConfig.plugin.step,
        })
    },
    filters: {
        getObje(data, temp) {
            return {
                id: data.id
            }
        }
    },
    methods: {
        getHeight() {
            const self = this;
            setTimeout(() => {
                const query = uni.createSelectorQuery().in(this);
                query.select('#ad').boundingClientRect(res => {
                    self.adHeight = res.height;
                }).exec();
            })
        },
        change(num) {
            let that = this;
            uni.showLoading({
                mask: true,
                text: '加载中...'
            });
            that.list = [];
            that.user = {};
            that._num = num;
            let url;
            if (num == 1) {
                url = that.$api.step.step_convert
            } else if (num == 2) {
                url = that.$api.step.ranking
            }
            that.getList(url);
        },
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.step.setting,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.setting = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList(url) {
            let that = this;
            that.$request({
                url: url,
                data: {
                    status: that._num
                }
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    that.user = response.data.user;
                    that.ad_data = response.data.ad_data;
                    if (that.list.length > 0) {
                        for (let i = 0; i < that.list.length; i++) {
                            that.list[i].raking = i + 1;
                        }
                        that.list[0].img = './../image/top1.png';
                        if (that.list.length > 1) {
                            that.list[1].img = './../image/top2.png';
                        }
                        if (that.list.length > 2) {
                            that.list[2].img = './../image/top3.png';
                        }
                    }
                    that.getHeight();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        let url = that.$api.step.step_convert;
        that._num = 1;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getSetting();
        that.getList(url);
    }
}
